@import url(https://fonts.googleapis.com/css2?family=Bungee+Shade&family=Montserrat:wght@400;800&display=swap);
:root {
	--bg: #111;
	--title: #0ff;
	--link: #14ff7e;
	--main-text: #fff;
}

body {
	background-color: #111;
	background-color: var(--bg);
	color: #fff;
	color: var(--main-text);
	font-family: "Montserrat", sans-serif;
}

a {
	color: #14ff7e;
	color: var(--link);
}
a:hover {
	color: #14ff7e;
	color: var(--link);
}


.title {
	font-size: 5rem;
	font-family: "Bungee Shade", "Monteserrat", sans-serif;
	color: var(--title);
}
.subtitle {
	font-size: 3rem;
	font-weight: 800;
}

/* Medium screen width breakpoint */
@media (max-width: 768px) {
	.title {
		font-size: 2.5rem;
	}
	.subtitle {
		font-size: 2rem;
	}
}

